body {
  .operators-wrap   { display: block !important; }
  #nav-icon         { display: block !important; }
  //#modal-news       { display: block  !important; }
  .back             { display: none  !important; }


  &.home {
    .main-keyboard  { display: block !important; }
    .small-keyboard { display: none  !important; }
    .check-begin    { display: none  !important; }
    .check-process  { display: none  !important; }
    .check-success  { display: none  !important; }
    .check-error    { display: none  !important; }
  }


  &:not(.home) {
    .small-keyboard { display: none !important; }
    .check-begin    { display: none !important; }
    .check-process  { display: none !important; }
    .check-success  { display: none !important; }
    .check-error    { display: none !important; }

    &.small-keyboard{ .small-keyboard{ display: block  !important; }}
    &.check-begin   { .check-begin   { display: block  !important; }}
    &.check-process { .check-process { display: block  !important; }}
    &.check-success { .check-success { display: block  !important; }}
    &.check-error   { .check-error   { display: block  !important; }}

                      .main-keyboard { display: none   !important; }
  }
}


// =============================== 1024px ===============================
