  body {
    //&.home      { #modal-news{ display: table !important; }}
    //&:not(.home){ #modal-news{ display: none  !important; }}
  }
}
// END ============================ 1024px ===============================



// =============================== 750px =================================
@media screen and (max-width: 750px) {
  body {
    &:not(.home){
      #nav-icon      { display: none  !important; }
      .operators-wrap{ display: none  !important; }
      .back          { display: block !important; }
      .wrap-nav-top  { display: none  !important; }
      .keyboard-box-wrap{ margin-top: 0 !important;}
    }
  }
}
// END ============================ 750px ================================
